import React, {Component, Fragment} from 'react';
import {CSSTransition, TransitionGroup} from 'react-transition-group';
import {Route} from 'react-router-dom';
import './Members.css';
import '../../../assets/Common.css'
import {Link} from 'react-router-dom';
import axios from 'axios';

import {
    Table,
    Button,
    Dropdown,
    UncontrolledButtonDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    Form,
    FormGroup,
    Input,
    Row,
    Col,
    Pagination,
    PaginationItem,
    PaginationLink
} from 'reactstrap';

import {withRouter} from 'react-router-dom';

class BasicMembers extends Component {
    constructor() {
        super();
        this.state = {
            userData: [],
            currentPage: 1,
            searchKeyword: '',
            itemsPerPage: 10,
            selectedItem: '아이디',
            userDataStore: []
        };
    }

    handleDropdownItemSelect = (item) => {

        this.setState({selectedItem: item});
    };

    updatePagination(data) {
        const { currentPage } = this.state;
        console.log('data.length : '+data.length);
        for (let i = 1; i < data.length / 10 + 1; i++) {

            console.log('currentPage : '+i);

            axios
            .get('https://test.ssirc.dev/Userprofile/paged/?page='+i)
            .then(pageResponse => {
                Promise
                    .all(pageResponse.data.results.map(user => {
                        data.forEach(element => {
                            if (element.User_id == user.User_id) {

                                var lastHrData = user.Hr[user.Hr.length - 1];
                                var lastSpo2Data = user.Spo2[user.Spo2.length - 1];
                                var lastAbnormalHrData = user.Stress[user.Stress.length - 1];
                                var lastStressData = user.Depress[user.Depress.length - 1];
                                var lastDepressData = user.Abnormal_hr[user.Abnormal_hr.length - 1];
                                var lastStepData = user.Step[user.Step.length - 1];
        
                                //to do : null 처리
                                lastHrData = lastHrData
                                    ? lastHrData.Hr
                                    : '미측정';
                                lastSpo2Data = lastSpo2Data
                                    ? lastSpo2Data.Spo2
                                    : '미측정';
                                lastAbnormalHrData = lastAbnormalHrData
                                    ? (lastAbnormalHrData.Abnormal_hr === true)
                                        ? 'Y'
                                        : 'N'
                                    : '미측정';
                                lastStressData = lastStressData
                                    ? lastStressData.Stress
                                    : '미측정';
                                lastDepressData = lastDepressData
                                    ? lastDepressData.Depress
                                    : '미측정';
                                lastStepData = lastStepData
                                    ? lastStepData.Step
                                    : '미측정';
        
                                if (lastDepressData === 3) 
                                    lastDepressData = "심각";
                                else if (lastDepressData === 2) 
                                    lastDepressData = "중증도";
                                else if (lastDepressData === 1) 
                                    lastDepressData = "경도";
                                else if (lastDepressData === 0) 
                                    lastDepressData = "정상";
                                
                                user.Gps_location = user.Gps_location
                                    ? user.Gps_location
                                    : 'no-idea';

                                element.hrData = lastHrData;
                                element.spo2Data = lastSpo2Data;
                                element.abnormalHrData = lastAbnormalHrData;
                                element.stressData = lastStressData;
                                element.depressData = lastDepressData;
                                element.stepData = lastStepData;
                            }
                        });
                        
                    }))
                    .then(() => {
                        this.setState({userDataStore: data});
                    })
                    .finally(()=>{
                        
                    });

            })
            .catch(userError => console.error('Error fetching userData', userError));
        }






    }

    componentDidMount() {
        this.loadData()
        //setInterval(this.loadData, 5000)
    }

    DetailButton = ({label}) => {
        return (
            <Button
                style={{
                    paddingTop: '3px',
                    paddingBottom: '3px',
                    paddingLeft: '15px',
                    paddingRight: '15px',
                    margin: '0px',
                    width: '100%'
                }}>
                {label}
            </Button>
        );
    };

    handleSearch = () => {
        const { userDataStore, searchKeyword, selectedItem } = this.state;
        console.log('userDataStore : '+userDataStore);
        console.log('searchKeyword : '+searchKeyword);
        const originalUserData = [...this.state.userDataStore];

        if (originalUserData) {
            const filteredData = originalUserData.filter(
                item => (selectedItem.includes('이름') && item.Name.includes(searchKeyword)) || (selectedItem.includes('전화번호') && item.Hp_num.includes(searchKeyword)) || (selectedItem.includes('아이디') && item.Id.includes(searchKeyword))
            );

            // 필터링된 결과를 페이지네이션 처리하여 currentItems에 할당
            this.setState({
                currentPage: 1, // 검색 시 첫 번째 페이지로 초기화
                userData: filteredData
            });
        }
    };

    SearchButton = ({label}) => {
        console.log('search button!');
        return (
            <Button
                color='secondary'
                style={{
                    width: '100%'
                }}
                onClick={this.handleSearch}>
                {label}
            </Button>
        );
    };

    async loadData() {
        axios
            .get('https://api.ssirc.dev/api/User/')
            .then(userResponse => {
                const usersWithAdditionalData = [];

                Promise
                    .all(userResponse.data.map(user => {

                        const userWithAdditionalData = {
                            ...user,
                            hrData: '미측정',
                            spo2Data: '미측정',
                            abnormalHrData: '미측정',
                            stressData: '미측정',
                            depressData: '미측정',
                            stepData: '미측정'
                        };
                        usersWithAdditionalData.push(userWithAdditionalData);

                    }))
                    .then(() => {
                        this.setState({userData: usersWithAdditionalData});
                        console.log('userData with additional data: ', usersWithAdditionalData);
                        this.setState({userDataStore: usersWithAdditionalData});
                        console.log('userDataStore with additional data: ', usersWithAdditionalData);
                        this.updatePagination(usersWithAdditionalData);
                    });
            })
            .catch(userError => console.error('Error fetching userData', userError));
    }

    render() {
        const {userData, currentPage, searchKeyword, itemsPerPage, selectedItem} = this.state;
        const totalPages = Math.ceil(userData.length / itemsPerPage);
        const indexOfLastItem = currentPage * itemsPerPage;
        const indexOfFirstItem = indexOfLastItem - itemsPerPage;
        const currentItems = userData.slice(indexOfFirstItem, indexOfLastItem);

        const paginate = pageNumber => {
            this.setState({currentPage: pageNumber});
        };

        return (
            <Fragment>
                <TransitionGroup>
                    <CSSTransition
                        component="div"
                        className="TabsAnimation"
                        appear={true}
                        timeout={0}
                        enter={false}
                        exit={false}>

                        <div className='whole-page-ui'>
                            <Row>
                                <Col>
                                    <p className='mb-0'>
                                        <span className='font-size-xxlarge mx-2'>나주시</span>
                                        <span className='font-size-xlarge'>건강 모니터링 시스템</span>
                                    </p>
                                </Col>
                                <Col className='font-size-small vertical-center horizontal-right'>Login &gt; 사용자 현황 관리</Col>
                            </Row>
                            <div className='space-10px'></div>
                            <div className='boader-black'></div>
                            <div className='space-20px'></div>
                            <Table className="mb-0" bordered={true}>
                                <tbody>
                                    <tr>
                                        <td className='font-size-large'>검색어</td>
                                        <td>
                                            <UncontrolledButtonDropdown>
                                                <DropdownToggle caret={true} color="secondary">
                                                    {selectedItem}
                                                </DropdownToggle>
                                                <DropdownMenu>
                                                    <DropdownItem onClick={() => this.handleDropdownItemSelect('아이디')}>아이디</DropdownItem>
                                                    <DropdownItem onClick={() => this.handleDropdownItemSelect('이름')}>이름</DropdownItem>
                                                    <DropdownItem onClick={() => this.handleDropdownItemSelect('전화번호')}>전화번호</DropdownItem>
                                                </DropdownMenu>
                                            </UncontrolledButtonDropdown>
                                        </td>
                                        <td>
                                            <FormGroup className='m-lg-0'>
                                                <Input
                                                    type="text"
                                                    id='keyword'
                                                    placeholder="검색어를 입력해 주세요"
                                                    value={searchKeyword}
                                                    onChange={(e) => this.setState({searchKeyword: e.target.value})}/>
                                            </FormGroup>
                                        </td>
                                        <td>
                                            <this.SearchButton label={'검색'}/>
                                        </td>
                                    </tr>
                                </tbody>
                            </Table>
                            <div className='space-30px'></div>
                            <Row>
                                <Col md='2' className='ms-auto text-center'>
                                    <Link to={`../../Infomation/Basic/`}>
                                        <Button className="mb-2 me-2 ms-auto" color='alternate' style={{ width: '100%' }}>전체 사용자 보기</Button>
                                    </Link>
                                </Col>
                            </Row>
                            <Table hover={true} className="text-center">
                                <thead>
                                    <tr>
                                        <th>순번</th>
                                        <th>ID</th>
                                        <th>이름</th>
                                        <th>핸드폰</th>
                                        <th>위치</th>
                                        <th>스트레스</th>
                                        <th>우울증</th>
                                        <th>심박이벤트여부</th>
                                        <th>혈중산소포화도</th>
                                        <th>심박수</th>
                                        <th>활동량</th>
                                        {/* <th>관리</th> */}
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        currentItems.map((item, index) => (
                                            <tr key={item.User_id}>
                                                <td>{item.User_id}</td>
                                                <td>{item.Id}</td>
                                                <td>{item.Name}</td>
                                                <td>{item.Hp_num}</td>
                                                <td>{item.Gps_location}</td>
                                                <td>{item.stressData}</td>
                                                <td>{item.depressData}</td>
                                                <td>{item.abnormalHrData}</td>
                                                <td>{item.spo2Data}</td>
                                                <td>{item.hrData}</td>
                                                <td>{item.stepData}</td>
                                                {/* <td>
                                                    <Link to={`../../Dashboards/basic/${item.User_id}`}>
                                                        <this.DetailButton label={'상세보기'}/>
                                                    </Link>
                                                </td> */}
                                            </tr>
                                        ))
                                    }
                                </tbody>
                            </Table>
                            <div className="pagination-container text-center">
                            <Pagination>
                                {
                                    Array
                                        .from({length: totalPages})
                                        .map((_, index) => (
                                            <PaginationItem key={index} active={index + 1 === currentPage}>
                                                <PaginationLink onClick={() => paginate(index + 1)}>
                                                    {index + 1}
                                                </PaginationLink>
                                            </PaginationItem>
                                        ))
                                }
                            </Pagination>
                            </div>
                        </div>
                    </CSSTransition>
                </TransitionGroup>
            </Fragment>
        )
    }
}
export default withRouter(BasicMembers);
